import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Form, Select, Col, Table, Button } from 'antd';
import { BtnSave } from 'components/common/button';
import { listPolicyObject } from 'dump/data';
import SearchBar from './SearchBar';
import './style.scss'

const table1 = [
  {
    title: 'Thao tc',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="primary">Chn</Button>
      </Fragment>
    ),
  },
  {
    title: 'M SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'H tn',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Tn i tng ',
    dataIndex: 'nameObject',
    key: 'nameObject',
  },
];

const table2 = [

  {
    title: 'M SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'H tn',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Lp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Tn i tng ',
    dataIndex: 'nameObject',
    key: 'nameObject',
  },
  {
    title: 'Thao tc',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="danger">Xa</Button>
      </Fragment>
    ),
  },
];

const { Option } = Select

@Form.create({ name: 'formListPolicyObject' })
class ListPolicyObject extends Component {
  state = {};

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Fragment>
        <Card title="Chn i tng chnh sch">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={listPolicyObject} columns={table1} />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sch i tng chnh sch">
          <Form style={{ textAlign: 'right' }} onSubmit={this.handleSubmit} layout="inline">
            <Form.Item label='Chn i tng chnh sch' >
              {getFieldDecorator(`objectSubsidy`, {
              })(
                <Select style={{ width: 300 }} >
                  <Option value="1">SVN Sinh vin thuc h ngho</Option>
                  <Option value="2">GDCS Gia nh chnh sch</Option>
                  <Option value="3">SVMC Sinh vin b ci</Option>
                  <Option value="4">SVTB Sinh vin con thng binh</Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item> <BtnSave htmlType="submit" /></Form.Item>
          </Form>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={listPolicyObject} columns={table2} />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPolicyObject />
  </Page>
);



